@font-face {
  font-family: 'Playfair Display';
  src: local('Playfair Display Regular'), url('../fonts/PlayfairDisplay-Regular.woff2') format('woff2'), url('../fonts/PlayfairDisplay-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Playfair Display';
  src: local('Playfair Display Bold'), url('../fonts/PlayfairDisplay-Bold.woff2') format('woff2'), url('../fonts/PlayfairDisplay-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Playfair Display';
  src: local('Playfair Display Bold Italic'), url('../fonts/PlayfairDisplay-BoldItalic.woff2') format('woff2'), url('../fonts/PlayfairDisplay-BoldItalic.woff') format('woff');
  font-weight: 700;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: local('Open Sans Regular'), url('../fonts/OpenSans-Regular.woff2') format('woff2'), url('../fonts/OpenSans-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: local('Open Sans Semi Bold'), url('../fonts/OpenSans-SemiBold.woff2') format('woff2'), url('../fonts/OpenSans-SemiBold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: local('Open Sans Bold'), url('../fonts/OpenSans-Bold.woff2') format('woff2'), url('../fonts/OpenSans-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: local('Open Sans Light'), url('../fonts/OpenSans-Light.woff2') format('woff2'), url('../fonts/OpenSans-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0 auto;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000;
  background-color: #fff;
}
details,
main {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
hr {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
code,
kbd,
pre,
samp {
  font-family: monospace,
    monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  text-decoration: none;
}
abbr[title] {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}
b,
strong {
  font-weight: bolder;
}
small {
  font-size: 80%;
}
img {
  border-style: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
[type=button],
[type=reset],
[type=submit],
button {
  -webkit-appearance: button;
}
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}
progress {
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type=checkbox],
[type=radio],
legend {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}
[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
summary {
  display: list-item;
}
[hidden],
template {
  display: none;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 22px;
  font-family: 'OpenSans', sans-serif;
  color: #fff;
  font-style: normal;
  font-weight: 400;
  background-color: #353436;
  position: relative;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
ul {
  padding: 0;
  margin: 0;
}
button {
  cursor: pointer;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  padding: 8px 21px 0px;
  text-transform: uppercase;
  background-color: #353436;
  z-index: 2;
  width: 100%;
  box-sizing: border-box;
  top: 0;
  padding-bottom: 16px;
}
.header__logo {
  padding-top: 15px;
  padding-bottom: 7px;
}
.header__nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav__list {
  display: flex;
}
.nav__user-in {
  text-align: right;
  line-height: 10px;
}
.nav__user-in__text {
  font-size: 14px;
  padding-right: 20px;
  font-family: 'OpenSans';
  background-image: url(../img/lock.png);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: 9px 13px;
  cursor: pointer;
}
.nav__list {
  max-width: 700px;
  display: flex;
  justify-content: right;
}
.nav__item {
  padding-left: 22px;
  cursor: pointer;
}
.nav__user-in__text,
.nav__item__link {
  transition: all 0.3s;
}
.nav__user-in__text:hover,
.nav__item__link:hover {
  color: #e2d1a6;
}
.nav__item__link--active {
  color: #e2d1a6;
  font-weight: 700;
}
.inside {
  position: relative;
}
.inside:hover .inside__list {
  display: block;
}
.inside__list {
  display: none;
  position: absolute;
  left: 15px;
  background-color: #353436;
}
.inside__item {
  padding: 3px 7px;
  transition: all 0.3s;
  font-size: 16px;
}
.inside__item:first-child {
  padding-top: 30px;
}
.inside__item:last-child {
  padding-bottom: 10px;
}
.inside__item:hover {
  color: #e2d1a6;
  text-shadow: 0 0 0.8px #e2d1a6, 0 0 0.8px #e2d1a6;
}
.inside__item:not(:first-child) {
  border-top: #fff 1px solid;
}
.main-content {
  position: relative;
  padding-top: 50px;
  height: 100vh;
  box-sizing: border-box;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 0.8s;
}
.main-content--bg-1 {
  background-image: url(../img/slide-2.jpg);
}
.main-content--bg-2 {
  background-image: url(../img/slide.png);
}
.main-content--bg-3 {
  background-image: url(../img/slide-3.jpg);
}
.main-content__block {
  margin: 332px 0 0 auto;
  background-color: rgba(255, 255, 255, 0.8);
  max-width: 510px;
  color: #000;
}
.main-content__block__heading {
  margin: 0;
  font-family: 'Playfair Display';
  font-style: italic;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 30px;
  padding: 19px 0px 0 30px;
}
.main-content__block__text {
  margin: 0;
  padding: 32px 30px;
}
.main-content__slider-img {
  position: absolute;
  bottom: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.main-content__slide-down {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main-content__slider-btn {
  width: 13px;
}
input[type='radio']:checked:after {
  width: 12px;
  height: 12px;
  border-radius: 8px;
  top: -2px;
  left: 0px;
  position: relative;
  background-color: #e2d1a6;
  content: '';
  display: inline-block;
  visibility: visible;
  border: 1px solid #000;
}
.slide-down {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
}
.slide-down--open {
  opacity: 1;
}
.slide-down--closed {
  display: none;
}
.main-content__slide-down__button {
  width: 87px;
  height: 28px;
  margin: 0 auto;
  border: none;
  background-color: #fff;
  margin-bottom: -1px;
  cursor: pointer;
}
.main-content__slide-down__button--closed {
  background-image: url(../img/open.png);
  background-repeat: no-repeat;
  background-position: center;
}
.main-content__slide-down__button--opened {
  background-image: url(../img/closed.png);
  background-repeat: no-repeat;
  background-position: center;
}
.main-content__slide-down__button:hover {
  border: none;
}
.main-content__slide-down__button:focus {
  border: none;
  outline: none;
}
.slide-down__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.slide-down__block:nth-child(1) {
  background-color: #e2d1a6;
  color: #000;
}
.slide-down__block:nth-child(2) {
  background-color: #fff;
  color: #000;
}
.slide-down__block:nth-child(3) {
  background-color: #000;
}
.slide-down__block__heading {
  font-family: 'Playfair Display';
  font-weight: 700;
  font-size: 28px;
  margin: 0;
  padding: 16px 13px;
}
.slide-down__block__text {
  margin: 0;
  padding: 2px 13px;
  font-size: 16px;
}
.slide-down__block__link {
  margin: 0;
  padding-right: 20px;
  padding: 10px 21px 10px 13px;
  text-align: right;
  font-size: 18px;
}
.slide-down__block:nth-child(1) .slide-down__block__link {
  color: #fff;
}
.popup {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0;
  z-index: 20;
  background: rgba(225, 225, 225, 0.6);
}
.popup--closed {
  display: none;
}
.popup__button {
  position: absolute;
  top: 12px;
  right: 10px;
  z-index: 2;
  display: block;
  width: 30px;
  height: 30px;
  border: none;
  cursor: pointer;
  background: transparent;
}
.popup__button::before,
.popup__button::after {
  content: "";
  position: absolute;
  top: 7px;
  left: -2px;
  width: 28px;
  height: 2px;
  background-color: #fff;
}
.popup__button::before {
  transform: rotate(45deg);
}
.popup__button::after {
  transform: rotate(-45deg);
}
.popup__button:hover {
  opacity: 0.8;
}
.popup__button:focus {
  outline: none;
}
.popup__heading {
  margin: 0;
  font-family: 'Playfair Display';
  font-weight: 700;
  text-transform: uppercase;
  font-size: 24px;
  padding-top: 7px;
  text-align: center;
}
.popup__form {
  display: flex;
  flex-direction: column;
  padding: 0px 35px;
}
.popup__form__container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 12px;
  padding: 0 38px 24px;
}
.popup__container {
  position: fixed;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #353436;
}
.footer {
  padding: 0 6px 6px;
  background-color: #353436;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  max-width: 942px;
  margin: 0 auto 0;
}
.footer__top__block {
  max-width: 195px;
}
.footer__top__block__heading {
  font-family: 'Playfair Display';
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  margin: 0;
  padding-top: 35px;
  padding-left: 5px;
}
.footer__top__block__text {
  font-family: 'Open Sans';
  font-weight: 300;
  font-size: 14px;
  margin: 0;
  padding-top: 16px;
  padding-left: 5px;
  line-height: 16px;
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
}
form {
  padding-top: 13px;
}
.form__legend__registration {
  margin-bottom: 10px;
}
.form__cognome,
.form__password,
.form__name,
.form__email {
  width: 178px;
  font-family: 'Open Sans';
  font-weight: 300;
  font-size: 14px;
  padding: 10px 0 6px 6px;
}
.form__cognome,
.form__password,
.form__email {
  margin-top: 5px;
}
.form__check {
  margin-right: 6px;
  height: 9px;
  width: 9px;
}
.form__check__text {
  font-family: 'Open Sans';
  font-weight: 300;
  font-size: 10px;
  margin: 7px 0 -5px 0;
}
.form__button {
  background-color: #000;
  color: #fff;
  border: none;
  outline: none;
  width: 184px;
  height: 34px;
}
.form__button__form {
  margin-top: 10px;
}
.form__button:hover {
  opacity: 0.8;
}
.info {
  display: flex;
  justify-content: left;
  font-size: 12px;
}
.info__item {
  margin-right: 17px;
}
.info__item__link {
  color: #fff;
  transition: all 0.3s;
}
.info__item__link:hover {
  color: #e2d1a6;
}
.social {
  display: flex;
  margin-left: auto;
  margin-right: 67px;
}
.social__item {
  padding: 0 6px;
}
.social__item__link:hover {
  opacity: 0.6;
}
.language {
  display: flex;
  font-family: 'Open Sans';
  font-size: 14px;
  height: 14px;
}
.language__item {
  line-height: 14px;
  text-transform: uppercase;
  padding: 0 9px;
}
.language__item:not(:first-child) {
  border-left: #fff 1px solid;
}
.language__item__link {
  color: #fff;
  transition: all 0.3s;
}
.language__item__link:hover {
  color: #e2d1a6;
}
